.column{
    margin: 0 auto;
    text-align: center;
    }
    h1{
        font-weight: 900;
    }
    span{
        color: lightskyblue;
    }
    img{
        margin: 0 auto;
    }
    .head{
        color: #333;
        font-weight: 900;
    }
    .head-text{
        color: gray;
        font-weight: 600;
    }
   .carousel-control-prev-icon,.carousel-control-next-icon{
       background-color: lightskyblue;
   }
   .rounded-circle{
       height: 150px;
   }
   .content{
       height: 400px;
       width: 100%;
       background-color: #fff;
   }
   .carousel{
       width: 100%;
   }
   .review-text{
       font-weight: 600;
       padding-top: 60px;
   }
   .review-stars{
       text-align: left;
       padding-left:45px ;
   }
   .job-post{
       text-align: left;
       padding-top: 20px;
       font-size: larger;
   }
   .rounded-circle {
       padding-top: 60px;
   }
   /*responsiveness*/
   @media only screen and (max-width: 600px) {
    .rounded-circle {
        padding-top: 0px;
    }
    .review-text{
        padding-top: 0px;
    }
    .review-stars{
        text-align: left;
        padding-left:50px ;
    }
    .job-post{
        text-align: left;
        padding-top: 20px;
        padding-left: 40px;
        font-size: larger;
        padding-bottom: 20px;
    }
    .content{
        height: auto;
    }
  }
